function xd_showToast(title, time,icon, callbackFun) {
	uni.showToast({
		title: title,
		duration:time,
		icon: icon ? icon : "none",
		success: function(res) {
			if (typeof(callbackFun) != "undefined" && callbackFun != null) {
				callbackFun(); // 
			}
		}
	});
}
