//
 function xd_timestampToTime(timestamp,times,times1,times2) {
  var date = new Date(timestamp);//10*1000131000
  var Y = date.getFullYear() + '-';
  var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1)  : date.getMonth()+1) + '-';
  var D = date.getDate() < 10 ? '0'+ date.getDate() : date.getDate();
  if(times){
	  var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':';
	  var m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':';
	  var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds() ;
	   return M+D+ ' '+h+m+s;
  }
  if(times1){
  	
	 var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':';
	 var m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':';
	 var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds() ;
  	  return Y+M+D+ ' ' + h+m+s;
  }
  if(times2){
  	
	 var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':';
	 var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes() ;
  	 return Y+M+D + ' '+ h+m;
  }
  return Y+M+D;
}
//

function xd_daysAddSub(date, num) {
            let d = new Date(date);
            let newD = new Date(d.setDate(d.getDate() + num));// +1 
			var Y = newD.getFullYear() + '-';
			var M = (newD.getMonth()+1 < 10 ? '0'+(newD.getMonth()+1) : newD.getMonth()+1) + '-';
			var D = newD.getDate() + ' ';
			var h = newD.getHours() + ':';
			var m = newD.getMinutes() ;
			
            return Y+M+D+h+m;
}

