function videoAdUtil(){
	let videoAd = null;
	
	 if (wx.createRewardedVideoAd) {
		 uni.showLoading({
		 	title:"...",
		 });
		 videoAd = wx.createRewardedVideoAd({ 
			 adUnitId: 'adunit-d579021705423692',
			 }) ;
			
			 videoAd.onError((err) => {
				  videoAd.offClose();
				 uni.hideLoading();	 
				 
			 }) ;
			 return videoAd;
	} ;
}
function  videoshowAd(videoAd) {
    if (videoAd) {
		videoAd.load()
		      .then(() => {
		        videoAd.show()
		      })
		      .catch(err => {
		        // ,
		       videoAd.load()
		        .then(() => {
		         videoAd.show()
		        })
		        .catch(err => {
					videoAd.offClose();
		          wx.showToast({
		            title: '',
		            icon: 'none',
					success: () => {
						uni.navigateBack({
							delta:1,
						})
					}
		          })
		        })
		      })
	}
   };
   
/**
 * 
 * @param rst
 * @returns {boolean}
 * @constructor
 */
function IsNullOrEmpty(rst) {
	if (rst != null && rst != "" && rst != undefined && Rst != "null") {
		var Rst = JSON.stringify(rst);
		if (Rst != "[]" && Rst != "null" && Rst != "{}" && Rst != "undefined") {
			return false;
		} else {
			return true;
		}
	} else {
		return true;
	}
}
/**
 * 
 */
function  showToast(isSuccess, content, methodes) {
	var msg = methodes + content;
	if (!IsNullOrEmpty(msg)) {
		uni.showToast({
			title: msg,
			icon: isSuccess ? "/static/images/suc.png" : "none"
		})
	}
}

